export default function CalcData(data, isIncludes) {
  // const { data } = props




  Object.keys(data.Bddy).forEach((BddyName) => {//BddyOp
    let UqOp = data.Bddy[BddyName].UqOp
    if (data.BddyOp[UqOp]) {//=UqOp
      if (!data.BddyOp[UqOp].Bddy) {//
        data.BddyOp[UqOp].Bddy = []
      }
      data.BddyOp[UqOp].Bddy.push(BddyName)
    }
  })

  data.ESklAb = {}//Eff
  data.list.SklAb.forEach((SA) => {
    Object.keys(data[SA]).forEach((name) => {
      if (data[SA][name].Eff) {
        data.ESklAb[name] = data[SA][name]
      }
    })
  })







  Object.keys(data.Chr).forEach((cName1, i) => {//Skl0()Skl1()
    Object.keys(data.Chr[cName1]).forEach((cName2) => {
      data.Chr[cName1][cName2].Skl1 = []
      if (data.Chr[cName1][cName2].Skl0) {
        data.Chr[cName1][cName2].Skl0.forEach((SklNam) => {
          if (Array.isArray(SklNam)) {
            data.Chr[cName1][cName2].Skl1.push(...SklNam)
          } else {
            data.Chr[cName1][cName2].Skl1.push(SklNam)
          }
        })
      }
    })
  })

  data.C1 = {}
  let Enmy = data.SlEnmy
  Object.keys(data.Chr).forEach((cName1) => {
    Object.keys(data.Chr[cName1]).forEach((cName2) => {

      console.log(data.Chr[cName1][cName2].memo)

      let cNameF = '' + cName2 + '' + cName1

      if (data.Chr[cName1][cName2].Rr === 'S') {
        data.C1[cNameF] = {//_Calc
          Ally: {
            Gr: data.Chr[cName1][cName2].Gr,
            Awk: 5,
            Lv: data.Lv[data.Chr[cName1][cName2].Rr] + data.EXLv,
            St: { Bas: {}, Org: {}, Btl: {} },
            Wepn: data.Chr[cName1][cName2].Wepn,
            Armr: data.Chr[cName1][cName2].Armr,
            Bddy: data.Chr[cName1][cName2].Bddy,
            Srs: {},
            Skl0: data.Chr[cName1][cName2].Skl0,//
            Skl1: data.Chr[cName1][cName2].Skl1,//
            // Skl: data.Chr[cName1][cName2].Skl,
            // Skl2: [
            //   { ...data.Skl[data.Chr[cName1][cName2].Skl[0]], No: 'S1', Nam: data.Chr[cName1][cName2].Skl[0], Str: 50 },
            //   { ...data.Skl[data.Chr[cName1][cName2].Skl[1]], No: 'S2', Nam: data.Chr[cName1][cName2].Skl[1], Str: 50 },
            //   { ...data.Skl[data.Chr[cName1][cName2].Skl[2]], No: 'S3', Nam: data.Chr[cName1][cName2].Skl[2], Str: 50 },
            // ],
            Skl3: [],
            Ab: data.Chr[cName1][cName2].Ab,
            UqP: [],
            ZHP: { V: 1, R: 100 },
          },
          Enmy: {
            St: {
              Bas: {},
              Org: {
                'HP': 0, 'TP': 0, '': 0, '': 0, '': 0, '': 0, '': 0
              },
              Btl: {}
            },
            Lv: data.Lv[data.Enmy[Enmy].Rr],
            Skl0: data.Chr[cName1][cName2].Skl0,//

            // Skl: data.Enmy[Enmy].Skl,
            Skl3: [],
            Ab: data.Enmy[Enmy].Ab,
            UqP: [],
            ZHP: { V: 1, R: 100 }
          }
        }


        data.Chr[cName1][cName2].Skl1.forEach((SklNam) => {//forEach
          let SklNum = data.Chr[cName1][cName2].Skl0.indexOf(SklNam) + 1//Skl0index+1
          if (SklNum < 1) {//
            data.Chr[cName1][cName2].Skl0.forEach((Skl0, i) => {//Skl0forEach
              if (Array.isArray(Skl0) && isIncludes(Skl0, SklNam)) {//Skl0
                SklNum = i + 1//Skl0index+1
                SklNum += Skl0.indexOf(SklNam) === 0 ? 'N' : 'M'//0Normal1Mutation
              }
            })
          }
          data.C1[cNameF].Ally.Skl3.push(
            { ...data.Skl[SklNam], Nam: SklNam, No: SklNum, Str: 50 }
          )
        })




        Object.keys(data.Chr[cName1]).forEach((cName22) => {//forEach//Skl3
          let Rr = data.Chr[cName1][cName22].Rr//

          data.Chr[cName1][cName22].Skl1.forEach((SklNam, i) => {//forEach
            // console.log(data.C1[cNameF][cName2])
            let Mt = false

            let SklNum
            if (isIncludes(data.Chr[cName1][cName22].Skl0, SklNam)) {//Mutation
              SklNum = data.Chr[cName1][cName22].Skl0.indexOf(SklNam)
            } else {//Mutation
              data.Chr[cName1][cName22].Skl0.forEach((Skl0, i) => {
                if (Array.isArray(Skl0)) {
                  if (isIncludes(Skl0, SklNam)) {
                    SklNum = i
                    if (Skl0.indexOf(SklNam) === 1) {
                      Mt = true
                    }
                  }
                }
              })
            }

            // console.log(SklNum, SklNam, Mt)

            if (
              (cName2 !== cName22) &&//
              (Rr !== 'S' || (Rr === 'S' && SklNum !== 2)) &&//S||S12
              (!Mt)//Mutation
            ) {
              data.C1[cNameF].Ally.Skl3.push(
                { ...data.Skl[SklNam], No: '', Nam: SklNam, Str: 50 }
              )
            }
          })//forEach



          // data.Chr[cName1][cName22].Skl.forEach((SklName, i) => {//forEach
          //   // console.log(data.C1[cNameF][cName2])
          //   if (
          //     (cName2 !== cName22) &&
          //     (data.Chr[cName1][cName22].Rr !== 'S' || (data.Chr[cName1][cName22].Rr === 'S' && i !== 2))
          //   ) {
          //     data.C1[cNameF].Ally.Skl3.push(
          //       { ...data.Skl[SklName], No: '', Nam: SklName, Str: 50 }
          //     )
          //   }
          // })//forEach

          data.Chr[cName1][cName22].UqP.forEach((AbNam) => {//
            data.C1[cNameF].Ally.UqP.push(AbNam)

            if (data.Ab[AbNam] && data.Ab[AbNam].Mag) {//MagSkl3
              data.C1[cNameF].Ally.Skl3.push(
                { ...data.Ab[AbNam], No: '', Nam: AbNam }//, Str: 50 
              )
            }
          })

        })//forEach//Skl3





        data.Chr[cName1][cName2].ExP.forEach((name, i) => {
          if (data.Ab[name] && data.Ab[name].Mag) {//MagSkl3
            data.C1[cNameF].Ally.Skl3.push(
              { ...data.Ab[name], No: '', Nam: name }//, Str: 50 
            )
          }
        })
        Object.keys(data.Chr[cName1][cName2].Ab).forEach((name, i) => {//forEach
          // if(cNameF===''){
          //   console.log(name)
          // }
          if (data.Ab[name] && data.Ab[name].Mag) {//MagSkl3
            data.C1[cNameF].Ally.Skl3.push(
              { ...data.Ab[name], No: '', Nam: name }//, Str: 50 
            )
          }
        })
        data.C1[cNameF].Ally.Skl = data.C1[cNameF].Ally.Skl3.map((SklEl) => {//Skl3Skl()
          return SklEl.Nam
        })





        data.Enmy[Enmy].Skl.forEach((SklName, i) => {
          data.C1[cNameF].Enmy.Skl3.push(
            { ...data.Skl[SklName], No: 'S' + (i + 1), Nam: SklName, Str: 50, Num: 1 }
          )
        })







      }
    })
  })

  // console.log(data.C1[''].Ally.Skl3)
  // console.log(data.C1[''].Ally.Skl3)



}