import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid, GridToolbar } from '@mui/x-data-grid';
import Button from '@mui/material/Button';

import ModalEqSlct from "./ModalEqSlct";
import ModalOpSlct from "./ModalOpSlct"
import CNSplit from "./CNSplit"
import { TextSrs } from './Text';



const WeArBdToTxt = { Wepn: '', Armr: '', Bddy: 'Buddy' }



export default function DataGridEq(props) {
  const { data, C, cNameF, Calc, setEqs } = props.props

  let cName1 = CNSplit(cNameF, 1)
  let cName2 = CNSplit(cNameF, 2)

  const columns = [
    {
      field: 'name',
      headerName: '',
      width: 220,//()
      renderCell: (params) => (
        <ModalEqSlct props={{ ...props.props, AE: 'Ally', Eq: params.row.Eq }} />
      )
    },
    {
      field: 'UqOp',
      headerName: '',
      width: 249,
    },
    {
      field: 'Op',
      headerName: '(x3)',
      width: 249,
      renderCell: (params) => (
        <ModalOpSlct
          name={params.row.name}
          Eq={params.row.Eq}
          OpB={params.row.Op}
          data={data}
          Calc={Calc}
        />
      )

    },

  ];

  const EqObj = {
    Wepn: data.C1[cNameF].Ally.Wepn,
    Armr: data.C1[cNameF].Ally.Armr,
    Bddy: data.C1[cNameF].Ally.Bddy,
  }


  const rows = Object.keys(EqObj).map((Eq, i) => {
    return ({
      id: EqObj[Eq],
      name: EqObj[Eq],
      Eq: Eq,
      UqOp: data[Eq][EqObj[Eq]].UqOp,
      Op: data[Eq][EqObj[Eq]].Op,
    })
  })



  // console.log(data.Chr[cName1][cName2].EqSet)

  return (
    <Box sx={{ height: 150, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        experimentalFeatures={{ newEditingApi: true }}
        density='compact'//
        autoHeight//
        hideFooter//
        hideFooterSelectedRowCount//
        hideFooterPagination//(
        getRowHeight={() => 'auto'}
      />
      {data.Chr[cName1][cName2].EqSet.map((NamSet, i) => {
        return (
          <Button
            key={i}
            variant="outlined"
            size='small'
            // sx={{pt:0.5,pr:0.5}} 
            onClick={() => {
              // console.log(NamSet)
              if (NamSet.includes('')) {
                if (NamSet === '' || NamSet === '') {
                  data.C1[cNameF].Ally.Wepn = Object.entries(data.Wepn).find(([name, el]) => el.Chr === cNameF)[0]
                }
                if (NamSet === '' || NamSet === '') {
                  data.C1[cNameF].Ally.Armr = Object.entries(data.Armr).find(([name, el]) => el.Chr === cNameF)[0]
                }
              } else {
                if (data.EqSet[NamSet]) {
                  data.C1[cNameF].Ally.Wepn = data.EqSet[NamSet].Wepn
                  data.C1[cNameF].Ally.Armr = data.EqSet[NamSet].Armr
                  data.C1[cNameF].Ally.Bddy = data.EqSet[NamSet].Bddy
                } else {
                  data.C1[cNameF].Ally.Wepn = Object.entries(data.Wepn).find(([name, el]) => el.Srs === NamSet)[0]
                  data.C1[cNameF].Ally.Armr = Object.entries(data.Armr).find(([name, el]) => el.Srs === NamSet)[0]
                  let BddyNamEl = Object.entries(data.Bddy).find(([name, el]) => el.Srs === NamSet)
                  if (BddyNamEl) {
                    data.C1[cNameF].Ally.Bddy = BddyNamEl[0]
                  }
                }
              }
              Calc()
            }}
          >
            {NamSet}
          </Button>
        )
      })}
    </Box >
  );
}