import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';
import Tooltip from '@mui/material/Tooltip';
import InfoIcon from '@mui/icons-material/Info';


const columns = [
  {
    field: 'EV',
    headerName: '',
    width: 250,
    renderHeader: (params) => (
      <Tooltip title="" placement="top-start" arrow >
        <InfoIcon color="disabled" />
      </Tooltip>
    ),
  },
  {
    field: 'JT',
    headerName: '',
    // headerAlign: 'center',
    align: 'center',
    width: 63,
    // sortable: false,//false:
    disableColumnMenu: true,//
    description: '',
  },
  {
    field: 'N',
    headerName: '',
    width: 200,
  },
  // {
  //   field: 'Ara',
  //   headerName: '',
  //   width: 150,
  //   align: 'center',
  // },

  // {
  //   field: 'DmgTxt',
  //   headerName: '',
  //   width: 150,
  //   align: 'center',
  // },
];


export default function DataGridBff(props) {
  const { data, cNameF, AE } = props.props

  const rows = data.C1[cNameF][AE].Eff9.map((EffEl, i) => {
    return ({
      ...EffEl,
      id: i,
    })
  })

  return (
    <Box sx={{ height: 300, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        experimentalFeatures={{ newEditingApi: true }}
        density='compact'//
        // autoHeight//
        hideFooter//
        hideFooterSelectedRowCount//
        hideFooterPagination//(
        getRowHeight={() => 'auto'}
      />
    </Box>
  );
}