import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';
import Tooltip from '@mui/material/Tooltip';
import InfoIcon from '@mui/icons-material/Info';

import { ImgIcon, ImgDmg } from './Img';
import StyleCommon from './StyleCommon';

export default function DataGridDmgChr(props) {
  const { data, cNameF, AE } = props.props

  const columns = [
    {
      field: 'No',
      headerName: '',
      width: 50,
      disableColumnMenu: true,//
      align: 'center',
    },
    {
      field: 'Nam',
      headerName: '',
      width: 162,//
      disableColumnMenu: true,//
    },
    {
      field: 'Ara',
      headerName: '',
      headerAlign: 'center',align: 'center',
      width: 70,
      disableColumnMenu: true,//
      renderCell: (params) => (
        <ImgIcon name={params.row.Ara} width={70} />//height={35}
      )
    },
    {
      field: 'Dmg',
      headerName: '',
      headerAlign: 'center',align: 'right',
      width: 120,
      disableColumnMenu: true,//
      renderHeader: (params) => (
        <>
          <Tooltip title="" placement="top-start" arrow >
            <InfoIcon color="disabled" />
          </Tooltip>
          
        </>
      ),
      renderCell: (params) => params.row.DmgTxt,
    },
    {
      field: 'DmgBar',
      headerName: '',
      width: 100,
      disableColumnMenu: true,//
      renderCell: (params) => (
        <ImgDmg
          data={data}
          Dmg={params.row.Dmg}
          HP={AE === 'Ally' ? data.Enmy[data.SlEnmy].St.Bas.HP : params.row.ZHP}
          AE={AE}
        />
      )
    },
    {
      field: 'BasDmgStr',
      headerName: '&(+10)',
      width: 248,//
      align: 'left',
      disableColumnMenu: true,//
    },
    {
      field: 'Amp',
      headerName: '',
      width: 50,
      align: 'center',
      disableColumnMenu: true,//
    },
    {
      field: '',
      headerName: '',
      headerAlign: 'center',
      width: 64,//1.1111
      align: 'center',
      disableColumnMenu: true,//
    },
    {
      field: '',
      headerName: '',
      width: 50,
      align: 'center',
      disableColumnMenu: true,//
    },
    {
      field: '',
      headerName: '',
      width: 50,
      align: 'center',
      disableColumnMenu: true,//
    },
  ];

  const rows = data.C1[cNameF][AE].Skl3.map((SklEl, i) => {
    return({
      ...SklEl,
      id: i,
      Elm: data.Skl[SklEl.Nam] ? data.Skl[SklEl.Nam].Elm : data.Ab[SklEl.Nam].Elm,
      DmgBar: SklEl.Dmg,
      ZHP: data.C1[cNameF].Ally.St.Btl.HP,
    })
  })

  return (
    <Box sx={{ height: 250, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        experimentalFeatures={{ newEditingApi: true }}
        density='compact'//
        // autoHeight//
        hideFooter//
        hideFooterSelectedRowCount//
        hideFooterPagination//(

        sx={StyleCommon}
        getRowClassName={(params) => params.row.Elm}


      // initialState={{
      //   pagination: {
      //     paginationModel: {
      //       pageSize: 5,
      //     },
      //   },
      // }}
      // pageSizeOptions={[5]}
      // checkboxSelection
      // disableRowSelectionOnClick
      // 

      />
    </Box>
  );
}