import * as React from 'react';
import Button from '@mui/material/Button';
import LoopIcon from '@mui/icons-material/Loop';

const SklAb = ['Skl', 'Ab']


export function ButtonBffClear(props) {
  const { data, Calc } = props.props
  return (
    <Button
      variant="outlined"
      color="error"
      fullWidth
      onClick={() => {
        SklAb.forEach((SA) => {
          Object.keys(data[SA]).forEach((name) => {
            if (data[SA][name].Eff) {
              data[SA][name].Eff.forEach((EffEl) => {
                EffEl.Stk = 0
              })
            }
          })
        })
        data.OthEff = {}
        Calc()
      }}
    >
      
    </Button>
  );
}

const NMTxt = Mt => Mt ? 'MUTATION' : 'NORMAL'

export function ButtonMt(props) {
  const { data, Calc } = props.props
  const [NM, setNM] = React.useState(NMTxt(data.Mt))

  let Disp = 'Normal'
  return (
    <Button
      variant="outlined"
      fullWidth
      onClick={() => {
        data.Mt = !data.Mt
        setNM(NMTxt(data.Mt))
        Calc()
      }}
    >
      <LoopIcon />{NM}
    </Button>
  );
}