import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid } from '@mui/x-data-grid';
import Tooltip from '@mui/material/Tooltip';
import InfoIcon from '@mui/icons-material/Info';

import { ImgIcon } from './Img';
import StyleCommon from './StyleCommon';
import RatingBff from "./RatingBff";

export default function DataGridChrSkl(props) {
  const { data, Calc, list, cNameF, id, V, t } = props.props

  const columns = [
    {
      field: 'No',
      headerName: '',
      minWidth: 49, width: 49,
      align: 'center',
    },
    {
      field: 'Nam',
      headerName: '',
      width: 125,
    },
    {
      field: 'Typ',
      headerName: '',
      minWidth: 49, width: 49,
    },
    {
      field: 'Knd',
      headerName: '',
      minWidth: 49, width: 49,
    },
    {
      field: 'TP',
      headerName: 'TP',
      headerAlign: 'right',
      align: 'right',
      minWidth: 45, width: 45,
    },
    {
      field: 'Elm',
      headerName: '',
      minWidth: 40, width: 40,
      align: 'center',
      renderCell: (params) => (
        <ImgIcon name={params.row.Elm} width={35} />//height={35}
      )
    },
    {
      field: 'Ara',
      headerName: '',
      headerAlign: 'center',
      width: 70,
      align: 'center',
      renderCell: (params) => (
        <ImgIcon name={params.row.Ara} height={35} />//height={35}
      )
    },
    {
      field: 'Txt',
      headerName: '',
      width: 300,
    },
    {
      field: 'UAra',
      headerName: '',
      headerAlign: 'center',
      minWidth: 35,width: 35,
      align: 'center',
      renderCell: (params) => (
        <ImgIcon name={params.row.UAra} height={35} />//height={35}
      )
    },
    {
      field: 'UTxt',
      headerName: '+UNION',
      width: 300,
    },
    {
      field: 'Eff',
      headerName: '',
      width: 600,
      renderHeader: (params) => (
        <>
          <Tooltip title="" placement="top-start" arrow >
            <InfoIcon color="disabled" />
          </Tooltip>
          
        </>
      ),
      renderCell: (params) => (
        <>
          {params.row.Eff.map((EffEl, i) => {
            return (
              <React.Fragment key={i}>
                {EffEl.E}
                <RatingBff props={{
                  ...props.props,
                  EffName: params.row.Nam,
                  EffEl: EffEl,
                }}
                />
              </React.Fragment>
            )
          })}
        </>
      )
    },


  ];

  const rows = [];
  data.C1[cNameF].Ally.Skl3.forEach((SklEl, i) => {
    if (!SklEl.Eff) SklEl.Eff = []
    rows.push({
      ...SklEl,
      id: SklEl.Nam + i,
    })
  });

  data.C1[cNameF].Ally.AbAllNamed.forEach((name, i) => {
    if (!data.Ab[name].Mag) {//Skl2
      rows.push({
        ...data.Ab[name],
        id: name + i,
        No: '',
        Nam: name,
        Eff: data.Ab[name].Eff ? data.Ab[name].Eff : [],
      })
    }
  });


  return (
    <Box sx={{ height: 750, width: '100%' }}>
      <DataGrid
        rows={rows}
        columns={columns}
        experimentalFeatures={{ newEditingApi: true }}
        density='compact'//
        // autoHeight//
        hideFooter//
        hideFooterSelectedRowCount//
        hideFooterPagination//(
        sx={StyleCommon}
        getRowClassName={(params) => params.row.Elm}
        getRowHeight={() => 'auto'}

      />
    </Box>
  );
}