import * as React from 'react';
import Box from '@mui/material/Box';
import { DataGrid, GridToolbar } from '@mui/x-data-grid';
import Button from '@mui/material/Button';

import ModalCommon from './ModalCommon';
import { ImgIcon, ImgDmg } from './Img';
import StyleCommon from './StyleCommon';
import CNSplit from "./CNSplit"


export default function DataGridDmg(props) {
  // const DataGridDmg = React.memo((props) => {
  // // const DataGridDmg = (props) => {
  const { data, Calc, setLS } = props.props



  const columns = [
    {
      field: 'Rr',
      headerName: '',
      minWidth: 40,
      width: 40,
      align: 'center',
      disableColumnMenu: true,//
      renderHeader: (params) => (''),

    },
    {
      field: 'name',
      headerName: '',
      width: 80,
      align: 'center',
      renderHeader: (params) => (''),

      renderCell: (params) => (
        <ModalCommon props={{ ...props.props, cNameF: params.row.name, id: '' }} C={props.C} />
      )
    },
    {
      field: 'Gr',
      headerName: '',
      minWidth: 40,
      width: 40,
      align: 'center',
      renderHeader: (params) => (''),


      renderCell: (params) => (
        <ImgIcon name={params.row.Gr} width={35} />
      )
    },
    {
      field: 'Typ',
      headerName: '',
      minWidth: 40,
      width: 40,
      align: 'center',
      renderHeader: (params) => (''),

      renderCell: (params) => (
        <ImgIcon name={params.row.Typ} width={35} />
      )
    },
    // {
    //   field: 'HP',
    //   headerName: 'HP',
    //   align: 'right',
    //   width: 55,
    // },
    // {
    //   field: 'TP',
    //   headerName: 'TP',
    //   align: 'right',
    //   width: 55,
    // },
    // {
    //   field: '',
    //   headerName: '',
    //   align: 'right',
    //   width: 55,
    // },
    // {
    //   field: 'D',
    //   headerName: '',
    //   align: 'right',
    //   width: 55,
    // },
    {
      field: '',
      headerName: '',
      minWidth: 49,
      width: 49,
      align: 'right',
      disableColumnMenu: true,//

    },
    // {
    //   field: 'I',
    //   headerName: '',
    //   align: 'right',
    //   width: 55,
    // },
    {
      field: '',
      headerName: '',
      align: 'center',
      minWidth: 49,
      width: 49,
      disableColumnMenu: true,//

    },
    {
      field: 'LS',
      headerName: '',
      width: 200,
      renderCell: (params) => (
        <Button
          variant="text"
          size='small'
          // name={params.row.LS}
          onClick={(event, newValue) => {
            // console.log(event.target.innerText)
            data.LS = event.target.innerText
            setLS(event.target.innerText)

            // data.C1[cNameF].Ally.Awk = newValue
            // setValue(newValue);
            Calc()
          }}


        >{params.row.LS}</Button>
      )

    },
    {
      field: 'Ara_1',
      headerName: '1',
      headerAlign: 'center',
      headerClassName: 'S1',

      align: 'center',
      width: 72,
      sortable: false,//false:
      disableColumnMenu: true,//


      renderCell: (params) => (
        <ImgIcon name={params.row.Ara_1} width={72} />
      )
    },
    {
      field: 'DmgBar_1',
      headerName: '',
      headerClassName: 'S1',
      width: 100,
      disableColumnMenu: true,//
      renderCell: (params) => (
        <ImgDmg
          data={data}
          Dmg={params.row.Dmg_1}
          HP={data.Enmy[data.SlEnmy].St.Bas.HP}
          AE='Ally'
        />
      )
    },
    {
      field: 'Ara_2',
      headerName: '2',
      headerAlign: 'center',
      headerClassName: 'S2',
      align: 'center',
      width: 72,
      sortable: false,//false:
      disableColumnMenu: true,//

      renderCell: (params) => (
        <ImgIcon name={params.row.Ara_2} height={35} />
      )
    },
    {
      field: 'DmgBar_2',
      headerName: '',
      headerClassName: 'S2',

      width: 100,
      disableColumnMenu: true,//

      renderCell: (params) => (
        <ImgDmg
          data={data}
          Dmg={params.row.Dmg_2}
          HP={data.Enmy[data.SlEnmy].St.Bas.HP}
          AE='Ally'
        />
      )
    },
    {
      field: 'Ara_3',
      headerName: '3',
      headerAlign: 'center',
      headerClassName: 'S3',

      align: 'center',
      width: 72,
      sortable: false,//false:
      disableColumnMenu: true,//

      renderCell: (params) => (
        <ImgIcon name={params.row.Ara_3} height={35} />
      )
    },

    {
      field: 'DmgBar_3',
      headerName: '',
      headerClassName: 'S3',

      width: 100,
      disableColumnMenu: true,//

      renderCell: (params) => (
        <ImgDmg
          data={data}
          Dmg={params.row.Dmg_3}
          HP={data.Enmy[data.SlEnmy].St.Bas.HP}
          AE='Ally'
        />
      )
    },
    {
      field: 'DmgMax',
      headerName: 'MAX',
      width: 100,
      disableColumnMenu: true,//

      renderCell: (params) => (
        <ImgDmg
          data={data}
          Dmg={params.row.DmgMax}
          HP={data.Enmy[data.SlEnmy].St.Bas.HP}
          AE='Ally'
        />
      ),
      description: '()',
    },
    {
      field: 'HDmgMax',
      headerName: 'MAX',
      width: 100,
      disableColumnMenu: true,//
      renderCell: (params) => (
        <ImgDmg
          data={data}
          Dmg={params.row.HDmgMax}
          HP={params.row.HP}
          AE='Enmy'
        />
      ),
      description: '(HP)',
    },
    {
      field: 'HDmgMaxRt',
      headerName: 'MAX',
      width: 100,
      disableColumnMenu: true,//

      // valueFormatter: (value) => `${value}% `,
      renderHeader: (params) => (
        ''
      ),

      renderCell: (params) => (
        <>{params.row.HDmgMaxRt + '%'}</>
      ),
      description: '',
    },



  ];





  const rows = []

  Object.keys(data.C1).forEach((cNameF, i) => {
    let cName1 = CNSplit(cNameF, 1)
    let cName2 = CNSplit(cNameF, 2)

    let DmgMax = 0
    data.C1[cNameF].Ally.Skl3.forEach((elSkl) => {
      DmgMax = Math.max(DmgMax, elSkl.Dmg)
    })

    let HDmgMax = 0
    data.C1[cNameF].Enmy.Skl3.forEach((elSkl) => {
      HDmgMax = Math.max(HDmgMax, elSkl.Dmg)
    })

    let HDmgMaxRt = Math.round(HDmgMax / data.C1[cNameF].Ally.St.Btl.HP * 100)

    let SklIndex = [0, 1, 2]
    data.C1[cNameF].Ally.Skl0.forEach((Skl0, i) => {

      if (Array.isArray(Skl0)) {
        let Mt = data.Mt ? 1 :0
        SklIndex[i] = data.C1[cNameF].Ally.Skl1.indexOf(Skl0[Mt])
      }
    })

    // data.C1[cNameF].Skl1.forEach((SklNam, i) => {//forEach
    //   // console.log(data.C1[cNameF][cName2])
    //   let Mt = false

    //   let SklNum
    //   if (isIncludes(data.Chr[cName1][cName2].Skl0, SklNam)) {//Mutation
    //     SklNum = data.Chr[cName1][cName22].Skl0.indexOf(SklNam)
    //   } else {//Mutation
    //     data.Chr[cName1][cName22].Skl0.forEach((Skl0, i) => {
    //       if (Array.isArray(Skl0)) {
    //         if (isIncludes(Skl0, SklNam)) {
    //           SklNum = i
    //           if (Skl0.indexOf(SklNam) === 1) {
    //             Mt = true
    //           }
    //         }
    //       }
    //     })
    //   }

    //   // console.log(SklNum, SklNam, Mt)

    //   if (
    //     (cName2 !== cName22) &&//
    //     (Rr !== 'S' || (Rr === 'S' && SklNum !== 2))&&//S||S12
    //     (!Mt)//Mutation
    //   ) {
    //     data.C1[cNameF].Ally.Skl3.push(
    //       { ...data.Skl[SklNam], No: '', Nam: SklNam, Str: 50 }
    //     )
    //   }
    // })//forEach



    rows.push({
      ...data.Chr[cName1][cName2],
      ...data.C1[cNameF].Ally.St.Btl,
      id: cNameF,
      name: cNameF,
      Dmg_1: data.C1[cNameF].Ally.Skl3[SklIndex[0]].Dmg,
      Dmg_2: data.C1[cNameF].Ally.Skl3[SklIndex[1]].Dmg,
      Dmg_3: data.C1[cNameF].Ally.Skl3[SklIndex[2]].Dmg,
      DmgBar_1: data.C1[cNameF].Ally.Skl3[SklIndex[0]].Dmg,
      DmgBar_2: data.C1[cNameF].Ally.Skl3[SklIndex[1]].Dmg,
      DmgBar_3: data.C1[cNameF].Ally.Skl3[SklIndex[2]].Dmg,
      Ara_1: data.C1[cNameF].Ally.Skl3[SklIndex[0]].Ara,
      Ara_2: data.C1[cNameF].Ally.Skl3[SklIndex[1]].Ara,
      Ara_3: data.C1[cNameF].Ally.Skl3[SklIndex[2]].Ara,
      Elm1: data.C1[cNameF].Ally.Skl3[SklIndex[0]].Elm,
      Elm2: data.C1[cNameF].Ally.Skl3[SklIndex[1]].Elm,
      Elm3: data.C1[cNameF].Ally.Skl3[SklIndex[2]].Elm,
      DmgMax: DmgMax,
      HDmgMax: HDmgMax,
      HDmgMaxRt: HDmgMaxRt,
    })
  })

  return (
    // <Box sx={{ height: 400, width: '100%' }}>
    <DataGrid
      rows={rows}
      columns={columns}
      experimentalFeatures={{ newEditingApi: true }}
      density='compact'//
      autoHeight//
      components={{
        Toolbar: GridToolbar,// 
      }}
      hideFooter//
      hideFooterSelectedRowCount//
      hideFooterPagination//(
      sx={StyleCommon}
      // getRowClassName={(params) =>  `super-app-theme--${params.row.Typ}`}
      getCellClassName={(params) => {
        let PF = params.field.split('_')[0]
        let n = params.field.split('_')[1]
        if (PF === 'Dmg' || PF === 'Ara' || PF === 'DmgBar') {// && params.value !== null&& params.row.Elm1 === ''
          return params.row['Elm' + n]// return params.value >= 15 ? 'hot' : 'cold';
        }
      }}


    // initialState={{
    //   pagination: {
    //     paginationModel: {
    //       pageSize: 5,
    //     },
    //   },
    // }}
    // pageSizeOptions={[5]}
    // checkboxSelection
    // disableRowSelectionOnClick

    />
    // </Box>
  );
}

