import * as React from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';


import TabParams from "./TabParams";
import CNSplit from "./CNSplit"
import { TextBr } from "./Text"



import { GridAmp, GridAdv, GridRs } from "./GridParams";

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Box
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box sx={{ p: 1 }}>
          {children}
        </Box>
      )}
    </Box>
  );
}

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

export default function TabAE(props) {
  const { data, cNameF, Calc } = props.props
  let cName1 = CNSplit(cNameF, 1)
  let cName2 = CNSplit(cNameF, 2)


  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box sx={{ height: 430, width: '100%' }}>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs value={value} onChange={handleChange} aria-label="basic tabs example" >
          <Tab label="" {...a11yProps(0)} />
          <Tab label="" {...a11yProps(1)} />
          <Tab label="MEMO" {...a11yProps(2)} />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        <TabParams props={{ ...props.props, AE: 'Ally' }} C={props.C} />
      </TabPanel>
      <TabPanel value={value} index={1}>
        <TabParams props={{ ...props.props, AE: 'Enmy' }} C={props.C} />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <TextBr text={data.Chr[cName1][cName2].memo} />

      </TabPanel>
    </Box>
  );
}